import maxImg from '../resources/max.png';
import beauImg from '../resources/beau.png';

let data = [
  {
    attributes: {
      ObjectId: 1,
      field_0: 'Max Payson',
      field_3: 'javascript, community, startups',
      field_5: 1529119335165,
      field_6: '10:00',
      field_7: '12:00'
    }
  },
  {
    attributes: {
      ObjectId: 2,
      field_0: 'Beau Ryck',
      field_3: 'geography, community, workplace',
      field_5: 1529219335165,
      field_6: '10:00',
      field_7: '12:00'
    }
  }
]
let attachmentPaths = [
  {
    id: 1,
    url: maxImg
  },
  {
    id: 2,
    url: beauImg
  }
]

class MockService{
  static loadFeatures(){
    return new Promise((res,rej) => res({features: data}))
  }
  static loadAttachment(feature){
    return new Promise((res,rej) => {
      const id = feature.attributes.ObjectId
      res(attachmentPaths[id-1])
    })
  }
}

export default MockService


